<template>
  <div>
    <router-view></router-view>
  </div>
</template>

<style>
    /*
 * Component: Info Box
 * -------------------
 */
.info-box {
  display: block;
  min-height: 95px;
  background: #F5F5F5;
  width: 100%;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  margin-bottom: 15px;
}
.info-box small {
  font-size: 12px;
}
.info-box .progress {
  background: rgba(0, 0, 0, 0.2);
  margin: 5px -10px 5px -10px;
  height: 2px;
}
.info-box .progress,
.info-box .progress .progress-bar {
  border-radius: 0;
}
.info-box .progress .progress-bar {
  background: #fff;
}
.info-box-icon {
  border-top-left-radius: 2px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 2px;
  display: block;
  float: left;
  height: 95px;
  width: 90px;
  text-align: center;
  font-size: 45px;
  line-height: 90px;
  background: rgba(0, 0, 0, 0.2);
}
.info-box-icon > img {
  max-width: 100%;
}
.info-box-content {
  padding: 5px 10px;
  margin-left: 90px;
}
.info-box-number {
  display: block;
  font-weight: bold;
  font-size: 18px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.progress-description,
.info-box-text {
  display: block;
  font-size: 12px;
  white-space: nowrap;
  overflow: hidden;
  font-weight: bold;
  text-overflow: ellipsis;
}
.info-box-text {
  text-transform: uppercase;
}
.info-box-more {
  display: block;
}
.progress-description {
  margin: 0;
}
.bg-green,
.bg-aqua,
.bg-red,
.bg-yellow {
  color: #fff !important;
}
.bg-green {
  background-color: #00a65a !important;
}
.bg-aqua {
  background-color: #00c0ef !important;
}
.bg-red {
  background-color: #dd4b39 !important;
}

.bg-yellow {
  background-color: #f39c12 !important;
}
</style>

<script>
import {mapState} from 'vuex'
import TopMenu from './components/Topmenu.vue'
export default {
  created () {
    console.log('App created.')
    if (this.$auth.isAuth()) {
      this.$store.dispatch('setUserToken', this.$auth.getUserToken())
    } else { // else redirect to login page
      this.$auth.destroyUser()
      this.$store.dispatch('clearAuthUser')
      this.$router.push({name: 'login'})
    }
  },

  components: {
    TopMenu
  },

  computed: {
    ...mapState({
      authUserStore: state => state.authUserStore
    })
  }
}
</script>



